        autor: photo,
        name: "无疑",
        creatTime: "2018-7-14",
        title:"Intel 公司解雇 CEO" ,
        content: "Intel 宣布与 CEO 解除合同，表面理由是他与女员工谈恋爱。但背后原因是这十年来，Intel 的新产品乏善可陈，PC 端止步不前，移动端完全败北，新兴的 AI 计算市场输给了 Nvidia。",
        imags: [],
        href: "https://wallstreetcn.com/articles/3342834",
        fromhreftxt: "阮一峰个人日志",
        fromhref: "http://www.ruanyifeng.com/blog/2018/07/weekly-issue-13.html"
      },
        {
        autor: photo,
        name: "无疑",
        creatTime: "2018-6-14",
        title:"Node创始人否定node" ,
        content: "Nodejs 的创始人 Ryan Dahl 一共做过两次关于 JS 的公开演讲。 一次是2009年宣布 Node 项目诞生，另一次是九年后的昨天，演讲题目是《Node 的设计失误》。\n" +
        "这个演讲的内容非常火爆，基本上把 Node 全部否定了，认为 libuv 和 npm（包括 package.json）都是设计错误，怪不得 JS 圈里面没人作声。他觉得，Node 已经无药可救了，所以动手写了一个新项目 deno（这个名字是 node 的拆分，表示 node 重组）。",
        imags: [],
