         autor:photo,
         name:"无疑",
         creatTime:"2018-6-5",
         title:"github被微软收购" ,
         content:"北京时间6月4日晚间，微软发布公告称，将以75亿美元的微软股票收购GitHub，微软公司副总裁Nat Friedman将出任GitHub首席执行官。GitHub官方推特转发确认了这一消息。",
