<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.actorItem{
  width: 100%;
  background-color: #fff;
  padding: 16px;
  box-sizing: border-box;
  border-radius: 2px;
  margin-bottom: 20px;
}
  .autor{
