  font-weight: 600;
}
.loction{
 margin-left: 15px;
  background-color: #FFB800;
  border-radius: 5px;
  font-size: 12px;
  padding:3px 5px;
  font-weight: 100;
  color: #fff;
}
.actorcon{
  font-size: 14px;
  text-indent: 30px;

}
  .imgs{
    max-height: 160px;
    overflow: hidden;
    margin: 10px 20px;
  }
  .imgitem{
    overflow: hidden;
    position: relative;
    float: left;
    width: 33.33%;
    box-sizing: border-box;
    padding: 0 10px 20px;
  }
  .imgitem img{
    width: 100%;
    float: left;
    height: auto;
    border-radius: 0;
    /*cursor: url(../images/open_new.cur), auto;*/
    border: 1px solid #f1f1f1;
  }
.actorfooter{
  margin-top: 10px;
  padding-top: 16px;
  border-top: 1px solid #f1f1f1;
  line-height:30px;
  text-align: right;
}
</style>



